import React, {ReactText} from "react";
import {
  Bar,
  CartesianGrid,
  ComposedChart,
  LabelList,
  Legend, ReferenceLine,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis
} from 'recharts';
import moment from "moment";
import './Panel.css';

const textColor = '#c5c5c5';
const formatDate = (d: ReactText) => {
  return moment(d).format('DD.MM.YYYY');
};

const renderLabel = (d: any) => {
  return d.value || '';
};

function Panel(props: any) {

  // @ts-ignore
  return (
	  <div>
		<h3>Romania COVID-19 cases</h3>
		<ResponsiveContainer width={'100%'} height={800}>
		  <ComposedChart
			  data={props.data}
			  margin={{top: 15, right: 35, bottom: 35, left: 35}}
			  style={{
				fontSize: 12,
				color: textColor,
				fontWeight: 400,
				letterSpacing: 0.2,
			  }}>
			<XAxis dataKey="Date" tickFormatter={formatDate} interval={0} angle={45} textAnchor={'start'}
				   stroke={textColor} />
			<YAxis stroke={textColor} />
			<Tooltip labelFormatter={formatDate}
					 contentStyle={{color: '#373737', textShadow: '1px 1px 2px #ddd'}}
					 isAnimationActive={false} />
			<CartesianGrid stroke={textColor} strokeDasharray={'4 4'} vertical={false} />
			<Legend verticalAlign={'bottom'} wrapperStyle={{bottom: 0}} />
			<Bar dataKey={'Recovered'} fill={'#009090'} stackId={'a'}>
			  <LabelList dataKey={'Recovered'} position={'insideTop'} content={renderLabel} />
			</Bar>
			<Bar dataKey={'Deaths'} fill={'#585858'} stackId={'a'}>
			  <LabelList dataKey={'Deaths'} position={'outside'} className={'label-deaths'}
						 content={renderLabel} />
			</Bar>
			<Bar dataKey={'Confirmed'} fill={'#ff7f7f'} stackId={'a'}>
			  <LabelList dataKey={'Confirmed'} position={'insideTop'} content={renderLabel} />
			</Bar>
			<ReferenceLine x={29} stroke={'#ff7f7f'} label="Full lockdown" className={'label-reference'} />
			<ReferenceLine x={26} stroke={'#585858'} label="First deaths" className={'label-reference'} />
			<ReferenceLine x={20} stroke={'#ff7f7f'} label="Emergency state" className={'label-reference'} />
			<ReferenceLine x={8} stroke={'#009090'} label="First recoveries" className={'label-reference'} />
			{/*<Line type="monotone" dataKey="Cases" stroke="#009090" />*/}
		  </ComposedChart>
		</ResponsiveContainer>
	  </div>
  );
}

export default Panel;
